/**=============================
	55. theme layout CSS Start
================================**/
body {
  &.dark-only {
    color: $dark-all-font-color;
    background-color: $dark-body-background;
    .apexcharts-tooltip.light {
      .apexcharts-tooltip-title {
        background: $dark-card-background;
        color: $white;
      }
    }
    hr {
      background-color: $dark-card-background;
    }
    .card {
      background-color: $dark-card-background;
    }
    .ecommerce-widget {
      border: 1px solid $dark-card-border !important;
    }
    .btn-light {
      color: $dark-all-font-color !important;
    }
    .btn-light.disabled {
      color: $black !important;
    }
    .b-r-light {
      border-color: $dark-card-border !important;
    }
    .form-select {
      color: $dark-all-font-color;
    }
    .list-group{
      li.list-group-item{
        border: 0;
      }
    }
    .add-project {
      .text-inherit {
        color: $dark-all-font-color;
      }
    }
    .knowledgebase-search {
      color: $theme-body-font-color;
      .form-control-plaintext {
        color: $theme-body-font-color;
      }
    }
    .btn-close {
      filter: brightness(0.8) invert(1);
    }
    .product-details-box {
      .price {
        span {
          color: $dark-all-font-color;
        }
      }
      .d-flex {
        border: 1px solid $dark-card-border;
      }
    }
    .pricingtable {
      box-shadow: 0 0 40px rgba($dark-card-border, 0.5);
    }
    .product-price {
      del {
        color: $dark-small-font-color;
      }
    }
    .ProfileCard {
      border: 1px solid $dark-card-border !important;
    }
    .form-control-plaintext {
      color: $dark-all-font-color;
    }
    .form-select {
      background-color: $dark-body-background;
      border: 1px solid $dark-border-color;
    }
    .box-layout {
      background-color: $dark-body-background;
      .page-wrapper,
      &.page-wrapper {
        .page-header {
          .header-wrapper {
            border: 1px solid $dark-border-color;
          }
        }
      }
    }
    .suggestion-box{
      background-color: $dark-body-background;
      ul{
        li{
          &:hover{
            background-color: $dark-card-border;
          }
          a{
            svg{
              stroke: $dark-all-font-color;
            }
            span{
              color: $dark-all-font-color;
            }
          }
        }
      }
    }
    .page-wrapper {
      .page-header {
        .header-wrapper {
          .nav-right {
            .onhover-show-div {
              .dropdown-title {
                h6 {
                  background-color: rgba($primary-color, 0.1);
                  color: $primary-color;
                  display: block;
                  text-align: start;
                }
              }
            }
          }
          .bookmark-flip {
            .bookmark-dropdown {
              .bookmark-content {
                .bookmark-icon {
                  border: 1px solid #323347;
                }
              }
            }
          }
        }
      }
    }
    .nav-right {
      .onhover-show-div:not(.profile-dropdown) {
        &.bookmark-flip{
          .bookmark-dropdown{
            .ProfileCard-avatar{
              svg{
                stroke: $dark-all-font-color !important;
              }
            }
            .ProfileCard-realName{
              i{
                color: $dark-all-font-color !important;
              }
            }
          }
        }
      }
    }
    .ProfileCard-realName { 
      .realname {
        color: $dark-all-font-color;
      }
    }
    .form-check-input {
      background-color: $dark-body-background;
    }
    .switch-state {
      &:before {
        background-color: $dark-background;
      }
    }
    .icon-state {
      .switch-state {
        &:after {
          color: $dark-all-font-color;
        }
      }
    }
    .form-switch {
      .form-check-input {
        background-color: $light;
        &:checked {
          background-color: $primary-color;
        }
      }
    }
    .todo-wrap{
      .card-header{
        &.b-bottom{
          border-bottom: 1px solid $dark-card-border;
        }
      }
    }
    .project-list {
      .border-tab {
        &.nav-tabs {
          .nav-item {
            .nav-link {
              border-bottom: $dark-card-border;
            }
          }
        }
      }
    }
    .product-box {
      background-color: $dark-card-background;
      .product-details {
        h4 {
          color: $dark-all-font-color;
        }
      }
      .modal {
        .modal-header {
          background-color: $dark-card-background;
        }
      }
    }
    .product-wrapper {
      &.sidebaron {
        .product-sidebar {
          .filter-section {
            .card {
              .left-filter {
                border-color: $dark-card-border;
              }
            }
          }
        }
      }
    }
    .page-wrapper {
      .page-body-wrapper {
        .page-body{
          .card:not(.email-body) {
            .table {
              &.table-light{
                tr{
                  th,td{
                    color: $theme-body-font-color;
                  }
                }
              }
            } 
          }
        }   
      }  
    }
    .product-page-main {
      p {
        color: $dark-all-font-color;
      }
      .pro-group {
        &:nth-child(n + 2) {
          border-color: $dark-card-border;
        }
      }
      .border-tab {
        &.nav-tabs {
          .nav-item {
            .nav-link {
              &.active {
                color: $white !important;
              }
            }
          }
        }
      }
    }
    .color-selector {
      ul {
        li {
          border-color: $dark-card-border;
        }
      }
    }
    .product-social {
      li {
        a {
          border-color: $dark-card-border;
        }
      }
    }
    .invoice {
      .invo-header {
        border-color: $dark-card-border;
      }
    }
    .animated-bg{
      i{
        background-color: $dark-card-background;
        box-shadow: 0 15px 30px 0 $dark-card-background;
      }
    }
    .product-name {
      h6 {
        color: $dark-all-font-color;
      }
    }
    .checkout {
      label {
        color: $dark-all-font-color;
      }
    }
    .email-wrap {
      .email-right-aside {
        .email-body {
          .inbox {
            .d-flex {
              &:hover {
                background-color: $dark-body-background;
              }
            }
          }
        }
      }
      .email-wrapper {
        .emailread-group {
          &:nth-child(n + 2) {
            border-color: $dark-card-border;
          }
        }
      }
    }
    .cke_chrome {
      border-color: $dark-card-border;
    }
    .support-table{
      border: 1px solid $dark-card-border;
      .rdt_TableHead{
        border-bottom: 1px solid $dark-card-border;
      }
      .rdt_Pagination{
        border-top: 1px solid $dark-card-border;
      }
      .rdt_TableRow:nth-child(odd){
        background-color: $dark-datatable-odd;
        color: $dark-all-font-color;
      }
      .rdt_TableRow:nth-child(even), .rdt_Pagination, .rdt_TableHeadRow{
        background-color: $dark-card-background;
        color: $dark-all-font-color;
      }
      .sc-eAKupa{
        button{
          fill: $dark-all-font-color;
        }
      }
    }
    .data-tables{
      .rdt_TableHead{
        .rdt_TableHeadRow{
          background-color: $dark-body-background;
          border-bottom: 1px solid $dark-card-border;
          .rdt_TableCol>div{
            color: $dark-all-font-color;
          }        
        }     
      }
      .rdt_TableBody{
        .rdt_TableRow{
          border-bottom: 1px solid $dark-card-border;
          .rdt_TableCell{
            background-color: $dark-body-background;
            color: $dark-all-font-color;
          }
          &:nth-child(odd){
            .rdt_TableCell{
              background-color: $dark-card-background;
            }
          }
        }
      }
      .rdt_Pagination {
        background-color: $dark-body-background;
        color: $dark-all-font-color;
        button{
          fill: $dark-all-font-color;
        }
      } 
    }
    .default-list-border{
      .list-group{
        li.list-group-item{
          border: 1px solid $dark-card-border;
        }
      }
    }
    .nav-tabs {
      border-bottom-color: $dark-card-border;
    }
    .grid-showcase {
      span {
        background-color: $dark-card-background;
        border: 1px solid $dark-card-border;
        color: $dark-all-font-color;
      }
    }
    .modal-dialog{
      &.static-modal{
        .modal-content{
          border-color: $dark-card-border;
          .modal-header, .modal-body{
            border-color: $dark-card-border;
          }
        }
      }
      .form-control {
        background-color: $dark-body-background;
        color: $dark-all-font-color;
        border-color: $dark-border-color;
      }
    }
    .vertical-timeline-element-content {
      background-color: $dark-body-background !important;
      .vertical-timeline-element-content-arrow {
        border-right: 7px solid $dark-body-background !important;
      }
    }  
    .vertical-timeline::before {
      background: $dark-body-background;
    }
    .draggable-normal{
      .smooth-dnd-draggable-wrapper{
        .react-trello-lane{
          background-color: $dark-body-background;
          .react-trello-card{
            background-color: $dark-card-background;
            border-color: $dark-card-border;
            header{
              color: $dark-all-font-color;
              border-color: $dark-card-border;
              button{
                color: $dark-all-font-color;
              }
            }
            div{
              color: $dark-all-font-color;
            }
          }
        }
      }
    }
    .swal2-modal {
			background-color: $dark-card-background;
			.swal2-title, .swal2-input {
				color: $dark-all-font-color;
			}
			.swal2-html-container {
				color: $dark-small-font-color;
			}
		}
    .deni-react-treeview-container{
      background-color: $dark-body-background;
      border-color: $dark-card-border;
    }
    #reactgooglegraph-3, #reactgooglegraph-5, #reactgooglegraph-9{
      rect{
        fill: $dark-card-background;
      }
    }
    .editor-toolbar{
      button{
        color: $dark-all-font-color;
        &:hover{
          background-color: $dark-body-background;
        }
      }
    }
    .react-datepicker{
      background-color: $dark-body-background;
      border-color: $dark-card-border;
      .react-datepicker__day-name{
        color: $dark-all-font-color;
      }
      .react-datepicker__day:hover{
        background-color: $dark-card-background;
      }
    }
    .datepicker-here {
      .react-datepicker {
        background-color: transparent;
      }
    }
    .react-datepicker__header {
      background-color: transparent;
    }
    .react-datepicker__time{
      background: transparent;
      color: $dark-all-font-color;
    }
    .react-datepicker__day {
      color: $dark-all-font-color;
    }
    .datepicker-here {
      .react-datepicker__day-name {
        color: $dark-all-font-color;
      }
    }
    .custom-profile {
      .card-footer {
        > div {
          h6 {
            color: $dark-all-font-color;
          }
        }
      }
    }
    .css-13cymwt-control {
      background-color: transparent;
    }
    .css-b62m3t-container {
      > div {
        background-color: $dark-body-background;
        color: $primary-color;
        border-color: $dark-card-border;
      }
    }
    .product-table {
      .rdt_TableHead {
        .rdt_TableHeadRow {
          background-color: $dark-card-background;;
        }
        .rdt_TableCol {
          > div {
            color: $dark-all-font-color;
          }
        }
      }
      .rdt_Pagination {
        background-color: $dark-card-background;
        Button {
          fill: $dark-small-font-color;
        }
        svg {
          fill: $dark-small-font-color;
        }
        select , span {
          color: $dark-all-font-color;
        }
      }
    }
    .product-table {
      h4 {
        color: $dark-small-font-color;
      }
      .rdt_TableBody {
        .rdt_TableRow {
          border-color: $dark-card-border;
          .rdt_TableCell {
            &:first-child {
              background-color: $dark-datatable-sorting-even;
            }
            &:nth-child(n+2) {
              background-color: $dark-datatable-sorting;
              color: $dark-all-font-color;
            }
          }
          &:nth-child(odd) {
            .rdt_TableCell {
              &:first-child {
                background-color: $dark-datatable-sorting;
              }
            }
          }
          &:nth-child(odd) {
            .rdt_TableCell {
              &:nth-child(n+2) {
                background-color: $dark-datatable-sorting-even;
                color: $dark-all-font-color;
              }
            }
          }
        }
      }
    }
    .order-history-table {
      .rdt_Table {
        border: 1px solid $dark-card-border;
      }
      .rdt_TableHeadRow {
        background-color: $dark-card-background;
        .rdt_TableCol {
          color: $dark-all-font-color;
        }
      }
      .rdt_TableBody {
        .rdt_TableRow {
          &:nth-child(odd) {
            background-color: $dark-datatable-sorting;
          }
          &:nth-child(even) {
            background-color: $dark-datatable-sorting-even;
          }
          &:hover {
            background-color: $dark-datatable-sorting;
          }
        }
      }
      .rdt_Pagination {
        background-color: $dark-card-background;
        Button {
          fill: $dark-small-font-color;
        }
        svg {
          fill: $dark-small-font-color;
        }
        select , span {
          color: $dark-all-font-color;
        }
      }
    }
    .invoice-table {
      .table-striped {
        tbody {
          tr {
            &:nth-of-type(odd) {
              td {
                label {
                  color: $dark-all-font-color;
                }
              }
            }
          }
        }
      }
    }
    // dashboard css
    .default-dash {
      p {
        color: $dark-small-font-color;
      }
    }
    .bestselling-sec {
      .card-header {
        border-color: $dark-card-border;
      }
    }
    .profitview-sec {
      svg {
        clipPath {
          rect {
            background-color: gray;
          }
        }
      }
    }
    .investment-sec {
      .icon {
        background-color: $primary-color;
        svg {
          color: $white;
        }
      }
      p {
        color: $dark-small-font-color;
      }
      &:hover {
        background-color: $primary-color !important;
        .icon {
          background-color: $white;
          svg {
            color: $primary-color;
          }
        }
      }
    }
    .browser-status {
      ul {
        &.browser-used {
          li {
            .browser-box {
              background-color: rgba($white, 0.05);
            }
            &.active {
              .browser-box {
                border-color: $dark-card-border;
                box-shadow: 0px 5px 22.5px rgba($dark-card-border, 0.8);
              }
            }
          }
        }
      }
    }
    .recent-activi {
      table {
        tbody {
          tr {
            td {
              .d-flex {
                svg {
                  fill: $dark-all-font-color;
                }
              }
            }
            &:hover {
              background-color: $dark-body-background;
              td {
                .d-flex {
                  svg {
                    fill: $primary-color;
                  }
                }
              }
            }
          }
        }
      }
    }
    .comment-box{
      .d-md-flex{
        h6{
          color: $dark-all-font-color;
        }
        img{
          border: 1px solid $dark-card-border;
        }
      }
    }
    .blog-single {
      .blog-details {
        .card {
          border-radius: 0 0 10px 10px;
        }
      }
    }
    // dashboard 02
    .chart_data_left,
    .chart_data_right {
      .card-body {
        .chart-main {
          .d-flex {
            border-color: $dark-card-border;
          }
        }
        h4 {
          color: $dark-all-font-color;
        }
      }
    }
    .chart_data_right {
      .card-body {
        .knob-block {
          input {
            color: $dark-all-font-color !important;
          }
        }
      }
    }
    .goal-view {
      ul {
        &.goal-list {
          border-color: $dark-card-border;
          li {
            border-color: $dark-card-border;
          }
        }
      }
    }
    .selling-product {
      .product-box {
        background-color: $dark-card-border;
      }
    }
    .datepicker--day-name {
      color: $white;
    }
    //general-widget
    .default-datepicker {
      .datepicker-inline {
        .datepicker {
          background: $dark-card-background;

          .datepicker--nav-title {
            color: $dark-all-font-color;

            i {
              color: $dark-all-font-color;
            }
          }

          .datepicker--content {
            .datepicker--days {
              .datepicker--days-names,
              .datepicker--cells {
                .datepicker--day-name,
                .datepicker--cell-day {
                  color: $dark-all-font-color;

                  &.-selected- {
                    color: $white;
                  }
                }
              }
            }
          }
        }
      }
    }
    .static-widget {
      h6 {
        color: $dark-all-font-color;
      }
    }
    .widget-joins {
      .widget-card {
        border-color: $dark-card-border;
      }
    }
    .widget-feedback {
      ul {
        border-color: $dark-card-border;
        li {
          &:nth-child(n + 2) {
            border-color: $dark-card-border;
          }
        }
      }
    }
    .custom-profile {
      .card-social {
        li {
          border-color: $dark-card-border;
        }
      }
      .card-footer {
        > div {
          div {
            + div {
              border-color: $dark-card-border;
            }
          }
        }
      }
      .profile-details {
        h4 {
          color: unset;
        }
      }
    }
    .activity-media {
      .d-flex {
        &:nth-child(n + 2) {
          border-color: $dark-card-border;
        }
        .flex-grow-1 {
          span {
            color: $dark-small-font-color;
          }
        }
      }
    }
    .social-widget-card {
      h6 {
        color: $dark-all-font-color;
      }
    }
    .user-profile {
      .follow {
        ul.follow-list {
          border-top: 1px solid $dark-card-border;
          li {
            &:nth-child(n + 2) {
              border-left: 1px solid $dark-card-border;
            }
          }
        }
      }
    }
    // Chart Widget Css
    .apexcharts-tooltip.light {
      .apexcharts-tooltip-title {
        background: $dark-card-background;
        color: $white;
      }
    }
    .apexcharts-canvas{
      span{
        color: $dark-all-font-color;
      }
      .apexcharts-xaxistooltip,
      .apexcharts-yaxistooltip{
        color: $dark-all-font-color;
        background: $dark-body-background;
        border: 1px solid $dark-card-border;
      }
      .apexcharts-tooltip{
        border: 1px solid $dark-card-border;
        background: $dark-body-background;
      }
      .apexcharts-tooltip-title{
        background: $dark-card-background;
        border-bottom: 1px solid $dark-card-border;
        color: $dark-all-font-color;
      }
    }
    .apexcharts-canvas {
      .apexcharts-yaxis {
        .apexcharts-yaxis-title {
          stroke: $dark-all-font-color;
        }
      }
    }
    .apexcharts-xaxistooltip-bottom {
      &::before,
      &::after {
        border-bottom-color: $dark-card-border;
      }
    }
    .apexcharts-xaxistooltip{
      background-color: $dark-body-background;
      color: $dark-all-font-color;
      border: 1px solid $dark-card-border;
    }
    .apexcharts-xaxistooltip-bottom:before {
      border-bottom-color: $dark-card-border;
    }
    .apexcharts-xaxistooltip-bottom:after {
      border-bottom-color: $dark-card-border;
    }
    // daterangepicker css start
    .daterangepicker {
      &:before {
        border-bottom-color: $dark-card-background;
      }
      .ranges {
        li {
          background-color: $dark-body-background;
          border-color: $dark-card-background;
          color: $dark-gray;
        }
      }
      .daterangepicker_input {
        .form-control {
          background-color: $dark-body-background;
          border-color: $dark-card-background !important;
        }
      }
      .calendar-time {
        select {
          border-color: $dark-card-background;
          background-color: $dark-body-background;
        }
      }
      &.dropdown-menu {
        background-color: $dark-card-background;
        -webkit-box-shadow: 0 0 14px $dark-body-background;
        box-shadow: 0 0 14px $dark-body-background;
      }
      .calendar-table {
        background-color: $dark-card-background;
        border-color: $dark-card-background;
      }
      &.ltr {
        td {
          &.in-range,
          &.off {
            background-color: $dark-body-background;
            color: $white;
          }
        }
      }
    }
    .datepicker--pointer {
      background-color: darken($primary-color, 20%);
      border-color: darken($primary-color, 20%);
    }
    .datepicker--nav {
      border-color: $light;
      border-bottom: none;
    }
    .datepicker--cell-day {
      color: $dark-all-font-color;
    }

    .datepicker {
      background-color: darken($primary-color, 20%);
    }

    .stepwizard {
      .stepwizard-row {
        .stepwizard-step {
          .btn-light {
            background-color: $dark-body-background !important;
            &.btn-primary {
              background-color: $primary-color !important;
            }
          }
        }
      }
    }

    // Support Ticket

    .support-ticket {
      .pro-gress {
        ul {
          li {
            color: rgba($white, 0.8);
          }
        }
      }
    }
    // form wizard
    .f1 {
      .f1-steps {
        .f1-progress {
          background-color: $dark-card-border;
        }
        .f1-step {
          .f1-step-icon {
            background-color: $dark-card-border;
            color: $dark-all-font-color;
          }
          &.active {
            .f1-step-icon {
              background-color: $primary-color;
            }
          }
        }
      }
