        }
      }
    }
    .filter-cards-view,
    .timeline-content {
      p {
        color: $dark-small-font-color;
      }
      .comment-number {
        i {
          color: $dark-small-font-color;
        }
      }
    }
    .social-chat {
      .flex-grow-1 {
        border: 1px solid $dark-card-border;
        &:after {
          border-right: 7px solid $dark-card-background;
        }
      }
      span {
        span {
          color: $dark-small-font-color;
        }
      }
    }
    .details-about {
      .your-details {
        p {
          color: $dark-small-font-color;
        }
      }
    }
    .activity-log {
      .my-activity {
        p {
          color: $dark-small-font-color;
        }
      }
    }
    .bookmark {
      ul {
        li {
          a {
            color: $dark-all-font-color;
          }
        }
      }
    }
    .bookmark-tabcontent {
      .tab-content {
        .card {
          .card-header {
            border-color: $dark-card-border;
          }
        }
      }
    }
    .list-persons {
      .profile-mail {
        .email-general {
          border-color: $dark-card-border;
        }
      }
    }
    .form-bookmark {
      .form-group {
        .select2-container {
          .select2-selection--single {
            border-color: $dark-card-border !important;
          }
        }
      }
    }
    .calendar-basic {
      #menu-navi {
        border-color: $dark-card-border;
      }
    }
    .wizard-4 {
      ul.anchor {
        background-color: $dark-card-background;
        li {
          h5 {
            color: $dark-all-font-color;
          }
          small {
            color: $dark-small-font-color;
          }
          .selected {
            h5 {
              color: $primary-color;
            }
            small {
              color: $primary-color;
            }
          }
        }
      }
      .step-container {
        background-color: $dark-body-background;
      }
    }

    //index page

    @media only screen and (max-width: 1199px) {
      .page-wrapper {
        &.compact-wrapper {
          .page-header {
            .header-wrapper {
              .toggle-sidebar {
                border-right: 1px solid $dark-card-border;
                background-color: $dark-body-background;
                svg {
                  stroke: $white;
                }
                &:hover {
                  background-color: $white;
                }
              }
            }
          }
        }
      }

      .page-wrapper {
        &.compact-wrapper {
          .left-header {
            .mega-menu {
              svg {
                stroke: $dark-all-font-color;
              }
            }
          }
        }
      }
    }

    @media only screen and (max-width: 767.98px) {
      .page-wrapper {
        &.compact-wrapper {
          .left-header {
            .mega-menu {
              svg {
                stroke: $white;
              }
              .nav-link {
                border-left: 1px solid $dark-card-border;
              }
            }
          }
        }
      }
      .left-header {
        .input-group {
          .form-control {
            background-color: $dark-card-background;
          }
        }
      }
    }
    .apexcharts-tooltip.light {
      border-color: $dark-card-border;
      background-color: $dark-body-background;
      color: $dark-all-font-color;
      .apexcharts-tooltip-title {
        border-color: $dark-card-border;
      }
      .apexcharts-tooltip-text-label,
      .apexcharts-tooltip-text-value {
        color: $white;
      }
    }
  }

  &.dark-sidebar {
    .toggle-sidebar {
      svg {
        stroke: var(--theme-default);
      }
    }

    .page-wrapper {
      .sidebar-main-title {
        p {
          color: $dark-all-font-color;
        }
      }

      &.compact-wrapper {
        .page-body-wrapper {
          div.sidebar-wrapper {
            background-color: darken($primary-color, 35%);
            .sidebar-main {
              .sidebar-links {
                li {
                  a {
                    span {
                      color: $dark-all-font-color;
                    }

                    svg {
                      stroke: $dark-all-font-color;
                    }
                  }
                }
                .menu-box {
                  background-color: darken($primary-color, 32%);
                  > ul{
                    > li{
                      .sidebar-link{
                        &:hover{
                          background-color: rgba($primary-color, 0.2);
                        }
                      }
                    }
                  }
                }
                > li {
                  .sidebar-link {
                    &.active {
                      background-color: rgba($primary-color, 0.2);
                      svg {
                        color: var(--theme-secondary);
                        stroke: var(--theme-secondary);
                      }
                    }
                  }

                  .mega-menu-container {
                    .mega-box {
                      .link-section {
                        .submenu-title {
                          h5 {
                            color: $dark-all-font-color;
                          }
                        }

                        .submenu-content {
                          &.opensubmegamenu {
                            li {
                              a {
                                color: $dark-all-font-color;
                                font-weight: 400;
                              }
                            }
                          }
                        }
                      }
                    }

                    &::after {
                      background-color: $light-all-font-color;
                    }
                  }

                  .sidebar-submenu {
                    li {
                      a {
                        color: $dark-all-font-color;
                      }
                    }
                  }
                }
                .sidebar-list {
                  ul.sidebar-submenu {
                    li {
                      a {
                        span {
                          color: $dark-all-font-color;
                        }

                        &.active {
                          color: var(--theme-default);
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          .according-menu {
            i {
              color: $dark-all-font-color;
            }
          }
        }
      }

      &.horizontal-wrapper {
        .page-body-wrapper {
          .sidebar-wrapper {
            background: $dark-card-background;
            .sidebar-main {
              background-color: $dark-card-background;
              .sidebar-links {
                > li {
                  > a {
                    color: $dark-all-font-color;
                  }
                  .sidebar-submenu {
                    background: $dark-card-background;
                    li {
                      a {
                        color: $dark-all-font-color;
                        &.active,
                        &:hover {
                          color: var(--theme-default);
                        }
                      }
                      .nav-sub-childmenu {
                        background: $dark-card-background;
                      }
                    }
                  }
                  .mega-menu-container {
                    background: $dark-card-background;
                    .mega-box {
                      .link-section {
                        .submenu-content {
                          li {
                            a {
                              color: $dark-all-font-color;
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
              .left-arrow,
              .right-arrow {
                background-color: $dark-card-background;
                color: $dark-all-font-color;
              }
            }
          }
        }
      }
    }
  }
  .for-dark {
    display: none;
  }
  &[class*="dark-"] {
    .for-dark {
      display: block;
    }
    .for-light {
      // display: none;
    }
  }
}
/**=====================
55. theme layout CSS ends
==========================**/