@media screen and (max-width: 575px) {
  .page-wrapper {
    &.compact-wrapper {
      .page-body-wrapper {
        div.sidebar-wrapper {
          .logo-wrapper {
            padding: 17px 20px !important;
            .back-btn {
              right: 22px !important;
            }
          }
          .sidebar-main {
            .sidebar-links {
              padding: 30px 26px !important;
              .menu-box {
                > ul {
                  > li {
                    a {
                      padding: 12px 14px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 576px) {
  .toggle-nav {
    i {
      font-size: 20px;
    }
  }
}
